import React, { useState, useEffect } from 'react';
import { Button } from '@material-ui/core';
import { Add } from '@material-ui/icons';
import { useToasts } from 'react-toast-notifications';

import Card from '../components/card';
import NoCard from '../components/nocard';
import Layout from './layout';
import api from '../api';
import QuickView from '../components/quickview';
import { useDispatch } from 'react-redux';
import { actions } from '../redux/_actions';

const Social = (props) => {
  const [limit] = useState(12);
  const [skip, setSkip] = useState(0);
  const [size, setSize] = useState(0);
  const [cards, setCards] = useState([]);
  const [quickViewCardIdx, setQuickViewCardIdx] = useState(-1);

  const dispatch = useDispatch();
  const { addToast } = useToasts();

  const showToast = (message, appearance = 'error') => {
    addToast(message, { appearance, autoDismiss: true });
  }

  useEffect(() => {
    loadCards();
  }, [])

  const loadCards = () => {
    dispatch(actions.setLoading(true));
    api.getProducts({ skip, limit, followed: 1 })
      .then((data) => {
        dispatch(actions.setLoading(false));
        if (!data || data.error) {
          return showToast(data.error);
        }
        setCards(data.data);
        setSize(data.size);
        setSkip(0);
      })
      .catch(ex => {
        console.log(ex);
        showToast('Failed loading items');
      })
  };

  const loadMore = () => {
    let toSkip = skip + limit;
    dispatch(actions.setLoading(true));
    api.getProducts({ skip: toSkip, limit, purchased: 1 })
      .then((data) => {
        dispatch(actions.setLoading(false));
        if (!data || data.error) {
          return showToast(data.error);
        }
        setCards([...cards, ...data.data]);
        setSize(data.size);
        setSkip(toSkip);
      })
      .catch(ex => {
        console.log(ex);
        showToast('Failed loading items');
      })
  };

  const loadMoreButton = () => {
    return (
      size > 0 &&
      size >= limit && (
        <Button onClick={loadMore} variant='contained' className='more-btn'>
          more <Add />
        </Button>
      )
    );
  };

  const quickView = (idx) => {
    setQuickViewCardIdx(idx);
  }
  const previousQuickView = () => {
    if (quickViewCardIdx < 1) return;
    setQuickViewCardIdx(quickViewCardIdx - 1);
  }
  const nextQuickView = () => {
    if (quickViewCardIdx > cards.length - 2) {
      loadMore();
      return ;
    }
    setQuickViewCardIdx(quickViewCardIdx + 1);
  }

  return (
    <Layout>
      <div>
        <div className='d-flex flex-wrap justify-content-center'>
          {cards.map((card, i) => (
            <div key={i} className={'shop-item '} onClick={() => quickView(i)}>
              <Card card={card} quickView={() => quickView(i)}/>
            </div>
          ))}
        </div>
        {loadMoreButton()}
      </div>
      {!cards.length &&<NoCard page="social" />}
      {(quickViewCardIdx > -1) && <QuickView card={cards[quickViewCardIdx]}
        editable={true}
        previous={previousQuickView}
        next={nextQuickView}
        close={()=>setQuickViewCardIdx(-1)} />}
    </Layout>
  );
};

export default Social;
